@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;400&display=swap);
.btn{
    padding: 8px 20px;
    border-radius: 2px;
    outline: none;
    border: none;
    cursor: pointer;
    transition: all 0.3s ease-out;
    font-size: 1.3rem;
    font-weight: bolder;
}

.btn-primary{
    border:1px solid black;
}

.btn:hover{
    cursor: pointer;
    border: 1px solid white;
    background-color: transparent;
    color: white;
    transition: all 0.3s ease-out;
}

.navbar {
    background: linear-gradient(90deg, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%);
    background-color: black;
    height: 80px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    font-size: 1.4rem;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 999;
    font-weight: 400;
  }
  
  .navbar-container {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    height: 80px;
    max-width: 1500px;
  }
  
  .navbar-logo {
    color: #fff;
    justify-self: start;
    margin-left: 20px;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    font-weight: 400;
  }
  
  .fa-mug-hot {
    margin-left: 0.5rem;
    font-size: 1.8rem;
  }
  
  .nav-menu {
    display: grid;
    grid-template-columns: repeat(4, auto);
    grid-gap: 10px;
    list-style: none;
    text-align: center;
    width: 60vw;
    -webkit-justify-content: end;
            justify-content: end;
    margin-right: 2rem;
  }
  
  .nav-item {
    height: 80px;
  }
  
  .nav-links {
    color: #fff;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    text-decoration: none;
    padding: 0.5rem 1rem;
    height: 100%;
    cursor: pointer;
  }
  
  .nav-item:hover {
    box-sizing: border-box;
    border-bottom: 4px solid #fff;
    transition: all 0.2s ease-out;
  }
  .nav-item:checked{
    transition: all 0.2s ease-out;

  }
  .active{
    border-bottom: 4px solid #fff;
  }
  .active:hover{
    border-bottom: none;
    transition: none;
  }

  .fa-bars {
    color: #fff;
  }
 
  .nav-links-mobile {
    display: none;
  }
  
  .menu-icon {
    display: none;
  }
  
  @media screen and (max-width: 1024px) {
    .NavbarItems {
      position: relative;
    }
    .fa-mug-hot{
      display: none;
    }
    .nav-menu {
      display: -webkit-flex;
      display: flex;
      -webkit-flex-direction: column;
              flex-direction: column;
      width: 100%;
      height: 90vh;
      position: absolute;
      top: 80px;
      left: -100%;
      opacity: 1;
      transition: all 0.5s ease;
    }
  
    .nav-menu.active {
      background: #242222;
      left: 0;
      opacity: 1;
      transition: all 0.5s ease;
      z-index: 1;
    }
  
    .nav-links {
      text-align: center;
      padding: 2rem;
      width: 100%;
      display: table;
    }
  
    .nav-links:hover {
      background-color: #fff;
      color: #242424;
      border-radius: 0;
    }
  
    .navbar-logo {
      position: absolute;
      top: 0;
      left: 0;
      -webkit-transform: translate(25%, 50%);
              transform: translate(25%, 50%);
    }
  
    .menu-icon {
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      -webkit-transform: translate(-100%, 60%);
              transform: translate(-100%, 60%);
      font-size: 1.8rem;
      cursor: pointer;
    }
  
    .fa-times {
      color: #fff;
      font-size: 2rem;
    }
  
    .nav-links-mobile {
      box-sizing: border-box;
      display: block;
      text-align: center;
      margin: 2rem auto;
      border-radius: 4px;
      width: 80%;
      text-decoration: none;
      font-size: 1.5rem;
      background-color: transparent;
      color: #fff;
      padding: 14px 20px;
      transition: all 0.3s ease-out;
      background: #fff;
      color: #242424;
    }
    .active{
      border:none;
    }
    .active:hover{border-bottom: none;}
    .nav-item:hover{border:none;}
  }
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Poppins', sans-serif;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
  }
  html{

    overflow-y: scroll;
    overflow-x: hidden;
  }


  .page {
    position: absolute;
    left: 15px;
    right: 15px;
  }
  
  .page-enter {
    opacity: 0;
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
  }
  
  .page-enter-active {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
    transition: opacity 300ms, -webkit-transform 500ms;
    transition: opacity 300ms, transform 500ms;
    transition: opacity 300ms, transform 500ms, -webkit-transform 500ms;
  }
  
  .page-exit {
    opacity: 0;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  
  .page-exit-active {
    opacity: 0;
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
    transition: opacity 300ms, -webkit-transform 300ms;
    transition: opacity 300ms, transform 300ms;
    transition: opacity 300ms, transform 300ms, -webkit-transform 300ms;
  }
  


  
  .home,
  .services,
  .products,
  .contact {
    text-align: center;
    position: relative;
    top:10rem;
  }

  .fade-enter {
    opacity: 0;
    z-index: 1;
  }
  
  .fade-enter.fade-enter-active {
    opacity: 1;
    transition: opacity 250ms ease-in;
  }


  .products {

    background-position: center;
    background-size: fill;
    background-repeat: no-repeat;
    color: #fff;
    font-size: 100px;
  }
  
  .sign-up {
   
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    color: #fff;
    font-size: 100px;
  }
